:root {
  --size: 18px;
  --squareWidth: 30px;
}
.dot {
  height: 18px;
  height: var(--size);
  width: 18px;
  width: var(--size);
  background-color: red;
  border-radius: 50%;
  text-align: center;
  position: absolute;
  bottom: -5px;
  left: 22px;
}
.square {
  height: 18px;
  height: var(--size);
  width: 30px;
  width: var(--squareWidth);
text-align: center;
  background-color: red;
  border-radius: 5px;
  position: absolute;
  bottom: -12px;
  left: 24px;
}

.container {
  position: relative;
}

.bottomright {
  position: absolute;
  bottom: 8px;
  left: 16px;
  /* font-size: 18px; */
}

