.login-page{width:360px;padding:8% 0 0;margin:auto}.login-page .form{position:relative;z-index:1;background:rgba(211,211,211,0.357);max-width:360px;margin:0 auto 100px;padding:45px;border-radius:5px;text-align:center;border:2px solid #27ae5f}.login-page .form input{font-family:"Roboto", sans-serif;border:0;box-sizing:border-box;font-size:14px}.login-page .form button{font-family:"Roboto", sans-serif;text-transform:uppercase;outline:0;background:#27ae5f;width:100%;border:0;border-radius:5px;font-size:14px;padding:15px;color:#ffffff;transition:all 0.3 ease;cursor:pointer}.login-page .form button p{margin:0px}.login-page .form button p a{font-size:14px;color:white;text-decoration:none}.login-page .form button:hover,.login-page .form button:active,.login-page .form button:focus{background:#219653}.login-page .form .message{margin:15px 0 0;color:#b3b3b3;font-size:12px}.login-page .form .message a{color:#4caf50;text-decoration:none}.login-page .form .register-form{display:none}.login-page .container{position:relative;z-index:1;max-width:300px;margin:0 auto}.login-page .container:before,.login-page .container:after{content:"";display:block;clear:both}.login-page .container .info{margin:50px auto;text-align:center}.login-page .container .info h1{margin:0 0 15px;padding:0;font-size:36px;font-weight:300;color:#1a1a1a}.login-page .container .info span{color:#4d4d4d;font-size:12px}.login-page .container .info span a{color:#000000;text-decoration:none}.login-page .container .info span .fa{color:#ef3b3a}.login-page body{background:#76b852;background:linear-gradient(to left, #76b852, #8dc26f);font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}#resend{font-size:12px;font-weight:bold;visibility:hidden}#resend:target{visibility:visible}.attensin{color:grey;margin:"5px 5px 15px 0"}

