@import url(https://fonts.googleapis.com/css?family=Roboto:300);

$color-primary: #27ae5f;
$color-primary-shadow: #08813a;
$color-second: #219653;
$choseSignInTypeHeight: 30;
$choseSignInTypeWidth: "80%";
.login-page {
  width: 360px;
  padding: 8% 0 0;
  margin: auto;
  .form {
    position: relative;
    z-index: 1;
    background: rgba(211, 211, 211, 0.357);
    max-width: 360px;
    margin: 0 auto 100px;
    padding: 45px;
    border-radius: 5px;
    // height: 320px;
    text-align: center;
    border: 2px solid $color-primary;
    // box-shadow: 0 0 20px 0 $color-primary-shadow, 0 5px 5px 0 $color-primary-shadow;
  }
  .form input {
    font-family: "Roboto", sans-serif;
    // outline: 0;
    // background: #f2f2f2;
    // width: 100%;
    border: 0;
    // margin: 0 0 15px;
    // padding: 15px;
    box-sizing: border-box;
    font-size: 14px;
  }
  .form button {
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    outline: 0;
    background: $color-primary;
    width: 100%;
    border: 0;
    border-radius: 5px;
    font-size: 14px;
    padding: 15px;
    color: #ffffff;
    -webkit-transition: all 0.3 ease;
    transition: all 0.3 ease;
    cursor: pointer;
    p {
      margin: 0px;
      a {
        font-size: 14px;
        color: white;
        text-decoration: none;
      }
    }
  }
  .form button:hover,
  .form button:active,
  .form button:focus {
    background: $color-second;
  }
  .form .message {
    margin: 15px 0 0;
    color: #b3b3b3;
    font-size: 12px;
  }
  .form .message a {
    color: #4caf50;
    text-decoration: none;
  }
  .form .register-form {
    display: none;
  }
  .container {
    position: relative;
    z-index: 1;
    max-width: 300px;
    margin: 0 auto;
  }
  .container:before,
  .container:after {
    content: "";
    display: block;
    clear: both;
  }
  .container .info {
    margin: 50px auto;
    text-align: center;
  }
  .container .info h1 {
    margin: 0 0 15px;
    padding: 0;
    font-size: 36px;
    font-weight: 300;
    color: #1a1a1a;
  }
  .container .info span {
    color: #4d4d4d;
    font-size: 12px;
  }
  .container .info span a {
    color: #000000;
    text-decoration: none;
  }
  .container .info span .fa {
    color: #ef3b3a;
  }
  body {
    background: #76b852; /* fallback for old browsers */
    background: -webkit-linear-gradient(right, #76b852, #8dc26f);
    background: -moz-linear-gradient(right, #76b852, #8dc26f);
    background: -o-linear-gradient(right, #76b852, #8dc26f);
    background: linear-gradient(to left, #76b852, #8dc26f);
    font-family: "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}
#resend {
  font-size: 12px;
  font-weight: bold;
  visibility: hidden;
}
#resend:target {
  visibility: visible;
}

